<template>
  <div v-if="!initDone">
    <q-skeleton type="QInput" width="100%" />
  </div>
  <template v-else>
    <TextArea1
      :autofocus="autofocus"
      v-if="dynamic_questions.type === 'textarea1'"
      :dense="false"
      v-model="answer.value"
      :required="dynamic_questions.required"
      :name="'question-' + dynamic_questions.id"
      :placeholder="question.placeholder || 'Enter a description...'"
      :rows="textareaRow"
    />
    <TextArea2
      :autofocus="autofocus"
      v-else-if="dynamic_questions.type === 'textarea2'"
      :dense="false"
      v-model="answer.value"
      :required="dynamic_questions.required"
      :name="'question-' + dynamic_questions.id"
      :placeholder="question.placeholder || 'Enter a description...'"
      :rows="textareaRow"
    />
    <InputTextField
      :autofocus="autofocus"
      v-else-if="dynamic_questions.type === 'textfield'"
      :dense="false"
      v-model="answer.value"
      :name="'question-' + dynamic_questions.id"
      :placeholder="question.placeholder || 'Your target audience'"
      :required="dynamic_questions.required"
    />
    <SelectOption
      :autofocus="autofocus"
      v-else-if="dynamic_questions.type === 'select'"
      :dense="false"
      :initModel="answer.value"
      v-model="answer.value"
      :selection="dynamic_questions.options"
      :required="dynamic_questions.required"
      :name="'question-' + dynamic_questions.id"
    />
    <SelectOption
      :autofocus="autofocus"
      v-else-if="dynamic_questions.type === 'select_multiple'"
      :dense="false"
      multiple
      :initModel="answer.value"
      v-model="answer.value"
      :selection="dynamic_questions.options"
      :required="dynamic_questions.required"
      :name="'question-' + dynamic_questions.id"
    />
    <RadioSelect
      v-else-if="dynamic_questions.type === 'radio'"
      :dense="false"
      :initModel="answer.value"
      :selection="dynamic_questions.options"
      :required="dynamic_questions.required"
      :name="'question-' + dynamic_questions.id"
      @updated="(v) => (answer.value = v)"
    />
    <CheckboxSelect
      v-else-if="dynamic_questions.type === 'checkbox'"
      :dense="false"
      :initModel="answer.value"
      :selection="dynamic_questions.options"
      :required="dynamic_questions.required"
      :name="'question-' + dynamic_questions.id"
      @updated="(v) => (answer.value = v)"
    />
    <SelectPlatforms
      v-else-if="dynamic_questions.type === 'select_platform'"
      :dense="false"
      :initModel="answer.value"
      :required="dynamic_questions.required"
      :platforms="platforms"
      :name="'question-' + dynamic_questions.id"
      @updated="(v) => (answer.value = v)"
    />
    <InputVideoWalkthrough
      :autofocus="autofocus"
      v-else-if="dynamic_questions.type === 'video_walkthrough'"
      :dense="false"
      class="full-width"
      :name="'question-' + dynamic_questions.id"
      :initModel="answer.value"
      :required="dynamic_questions.required"
      @updated="(v) => (answer.value = v)"
    />
    <SelectBrand
      :autofocus="autofocus"
      v-else-if="dynamic_questions.type === 'select_brand'"
      :dense="false"
      v-model="answer.value"
      :initModel="answer.value"
      :name="'question-' + dynamic_questions.id"
      :required="dynamic_questions.required"
      @brand-added="(e) => (answer.value = e)"
      @brand-finished="(e) => (answer.value = e)"
      @brand-form-closed="
        () => {
          if (answer.value && answer.value?.id === 0) answer.value = '';
        }
      "
    />
    <SelectCategoryType
      :autofocus="autofocus"
      v-else-if="dynamic_questions.type === 'select_taskcategory'"
      :dense="false"
      type="task"
      v-model="answer.value"
      :initModel="answer.value"
      :required="dynamic_questions.required"
    />
    <SelectCategoryType
      :autofocus="autofocus"
      v-else-if="dynamic_questions.type === 'select_projectcategory'"
      :dense="false"
      type="project"
      v-model="answer.value"
      :initModel="answer.value"
      :required="dynamic_questions.required"
    />
    <SelectTaskProjectType
      v-else-if="dynamic_questions.type === 'select_tasktype'"
      :autofocus="false"
      :dense="false"
      type="task"
      v-model="answer.value"
      :initModel="answer.value"
      :required="dynamic_questions.required"
    />
    <SelectTaskProjectType
      v-else-if="dynamic_questions.type === 'select_projecttype'"
      :autofocus="false"
      :dense="false"
      type="project"
      v-model="answer.value"
      :initModel="answer.value"
      :required="dynamic_questions.required"
    />
    <InputWebsite
      :autofocus="autofocus"
      v-else-if="dynamic_questions.type === 'url'"
      :dense="false"
      v-model="answer.value"
      :required="dynamic_questions.required"
    />
    <InputPhoneNumber
      :autofocus="autofocus"
      v-else-if="dynamic_questions.type === 'phone'"
      :dense="false"
      :required="dynamic_questions.required"
      @areaCodeChanged="
        (e) => {
          answer.value = answer.answer
            ? `${e}+${answer.value?.split('+')[1] || ''}`
            : e;
        }
      "
      @numberChanged="
        (e) => {
          answer.value = answer.answer
            ? `${answer.value?.split('+')[0] || ''}+${e}`
            : e;
        }
      "
      :areaCode="answer.value.split('+')[0]"
      :phoneNumber="answer.value.split('+')[1]"
    />
    <FileUploader
      :autofocus="autofocus"
      v-else-if="
        dynamic_questions.type === 'upload_single' ||
        dynamic_questions.type === 'upload_multiple'
      "
      :dense="false"
      :multiple="dynamic_questions.type === 'upload_multiple'"
      temporary
      withItems
      miniItems
      max-file-size="10000000"
      :note="question?.options?.note || ''"
      :accept="
        question?.options?.accept ||
        'image/*, woff, woff2, otf, ttf, psd, eps, ai, xd, fig'
      "
      @added="(files) => $emit('fileAdded', files)"
      @uploaded="({ files, xhr }) => $emit('fileUploaded', { files, xhr })"
      @removed="(files) => $emit('fileRemoved', files)"
      :factory="fileFactory"
      :removedFile="fileRemovedFile"
      :initSlots="fileInitSlots"
      :thumbnail="fileThumbnail"
    />
  </template>
</template>

<script>
import { defineComponent, defineAsyncComponent } from 'vue';
import { DynamicQuestions_Mixin } from 'src/mixins';
import { objectCopy } from 'src/assets/scripts/functions';

export default defineComponent({
  name: 'DynamicQuestions',

  emits: ['answered', 'fileAdded', 'fileUploaded', 'fileRemoved'],

  mixins: [DynamicQuestions_Mixin],

  components: {
    SelectBrand: defineAsyncComponent(() =>
      import('src/components/Brands/SelectBrand.vue')
    ),
    SelectPlatforms: defineAsyncComponent(() =>
      import('src/components/Helpers/SelectPlatforms.vue')
    ),
    RadioSelect: defineAsyncComponent(() =>
      import('src/components/Helpers/RadioSelect.vue')
    ),
    CheckboxSelect: defineAsyncComponent(() =>
      import('src/components/Helpers/CheckboxSelect.vue')
    ),
    InputTextField: defineAsyncComponent(() =>
      import('src/components/Helpers/InputTextField.vue')
    ),
    TextArea1: defineAsyncComponent(() =>
      import('src/components/Helpers/TextArea1.vue')
    ),
    TextArea2: defineAsyncComponent(() =>
      import('src/components/Helpers/TextArea2.vue')
    ),
    InputVideoWalkthrough: defineAsyncComponent(() =>
      import('src/components/Helpers/InputVideoWalkthrough.vue')
    ),
    SelectOption: defineAsyncComponent(() =>
      import('src/components/Helpers/SelectOption.vue')
    ),
    InputWebsite: defineAsyncComponent(() =>
      import('src/components/Helpers/InputWebsite.vue')
    ),
    InputPhoneNumber: defineAsyncComponent(() =>
      import('src/components/Helpers/InputPhoneNumber.vue')
    ),
    FileUploader: defineAsyncComponent(() =>
      import('src/components/Helpers/FilesUploader.vue')
    ),
    SelectCategoryType: defineAsyncComponent(() =>
      import('src/components/Helpers/SelectCategoryType.vue')
    ),
    SelectTaskProjectType: defineAsyncComponent(() =>
      import('src/components/Helpers/SelectTaskProjectType.vue')
    ),
  },

  props: {
    autofocus: {
      type: Boolean,
      default: false,
    },
    question: {
      type: Object,
      default: () => {},
    },
    initAnswer: {
      type: Object,
      default: () => {},
    },
    platforms: {
      type: Array,
      default: () => [],
    },
    fileThumbnail: {
      type: String,
      default: 'auto',
    },
    textareaRow: {
      type: Number,
      default: 15,
    },
    fileRemovedFile: {},
    fileFactory: {},
    fileInitSlots: {},
  },

  data() {
    return {
      answer: {},
      initDone: false,
    };
  },

  watch: {
    ['answer']: {
      async handler(val) {
        this.$emit('answered', val);
      },
      deep: true,
    },
  },

  computed: {
    dynamic_questions() {
      const question = objectCopy(this.question);
      if (this.initDone !== true) question.required = false;
      return question;
    },
  },

  mounted() {
    this.initDone = false;
    if (this.initAnswer) this.answer = this.initAnswer;
    this.init();
  },

  methods: {
    init() {
      if (!this.question?.id) return;
      if (!this.answer?.value)
        this.answer = this.formatDynamicAnswer(this.question);
      this.initDone = true;
    },
  },
});
</script>
